












import Vue from "vue";
import Hand from './Hand.vue';

export default Vue.extend({
  name: "Square",
  props: {
    hands: Array,
    hand: Number,
  },
  components: {
    Hand
  },
  computed: {
    remains(): number {
      return this.$store.state.deck.length
    }
  },
  methods: {
    isSelected(index: number): boolean {
      return index === this.hand
    }
  },
  // mounted() {}
});
