







import Vue from "vue";
import RowVue from '@/components/Row.vue'
import SquareFill from '../library/SquareFill';
import Row from '../library/Row';

export default Vue.extend({
  name: "Hand",
  props: {
    hand: Number,
    isSelected: Boolean,
  },
  components: {
    RowVue
  },
  computed: {
    row(): Row {
      const cardType = this.hand
      return SquareFill.sFrom(cardType)
    }
  },
  methods: {
  },
  // mounted() {}
});
