












import Vue from "vue";
import Square from "./Square.vue";

export default Vue.extend({
  name: "Row",
  props: {
    row: Array,
    floatingLefts: {
      type: Array,
      default: () => [],
    },
  },
  components: {
    Square
  },
  methods: {
    isFloatingAt(i: number): boolean {
      return this.floatingLefts.includes(i)
    }
  },
  // mounted() {}
});
